import React, { Fragment } from "react";
import Media from "components/Media/Media";
import Button from 'components/CustomButtons/Button.js';
import CustomInput from "components/CustomInput/CustomInput";
import Reply from "@material-ui/icons/Reply";
import { Icon, makeStyles } from "@material-ui/core";
import sectionCommentsStyle from "assets/jss/material-kit-pro-react/views/blogPostSections/sectionCommentsStyle.js";

const useStyles = makeStyles(sectionCommentsStyle);

const ChatInput = ({ avatar, value, onChange, onReply, onNotify, enableNotify }) => {

    const classes = useStyles();

    return (
        <Fragment>
            <Media
                avatar={avatar}
                body={
                    <CustomInput
                        id="reply"
                        formControlProps={{
                            fullWidth: true
                        }}
                        inputProps={{
                            multiline: true,
                            rows: 4,
                            placeholder: " Write some nice stuff or nothing...",
                            onChange: onChange,
                            value: value,
                        }}
                    />
                }
                footer={
                    <div>
                        <Button
                            color="github"
                            className={classes.footerButtons}
                            onClick={onReply}
                        >
                            <Reply /> Reply
                        </Button>
                        <div style={{ float: "right" }}>
                            <div style={{ width: "5px", height: "41px" }} ></div>
                        </div>

                        {enableNotify ? <Button
                            className={classes.footerButtons}
                            justIcon round color="github"
                            onClick={onNotify}>
                            <Icon>ring_volume</Icon>
                        </Button> : <></>}
                    </div>
                }
            />
        </Fragment>
    )
}

export default ChatInput;