import React, { useEffect, useState } from "react";
import { useSelector } from "react-redux";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
// core components
import GridContainer from "components/Grid/GridContainer.js";
import GridItem from "components/Grid/GridItem.js";
import ChatMessage from "components/Chat/ChatMessage";
import ChatInput from "components/Chat/ChatInput";
import marc from "assets/img/faces/marc.jpg";
import * as paths from "routes/constUrl";
import { withRouter } from "react-router-dom";
import FloatyAssistant from "components/Assistant/floaty";
import sectionCommentsStyle from "assets/jss/material-kit-pro-react/views/blogPostSections/sectionCommentsStyle.js";

const useStyles = makeStyles(sectionCommentsStyle);

const ChatDefaultPage = ({ setSesameOpen, history }) => {
    const classes = useStyles();

    const list = [0, 1, 2];
    const [inputValue, setInputValue] = useState("");

    useEffect(() => {
        if (inputValue === "520") {
            setSesameOpen(true);
        }
    }, [inputValue])

    return (
        <div className={classes.section}>
            <GridContainer justify="center">
                <GridItem xs={12} sm={8} md={8}>
                    <div>
                        {list.map((v, i) => {
                            return (
                                <ChatMessage
                                    key={i}
                                    avatar={marc}
                                    title={<>Alec Thompson <small>· 3 days ago</small></>}
                                    message={"Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Amet mauris commodo quis imperdiet massa tincidunt nunc pulvinar sapien. Malesuada fames ac turpis egestas sed tempus urna et."}
                                />
                            )
                        })}
                        <ChatInput
                            avatar={marc}
                            onChange={(e) => { setInputValue(e.target.value) }}
                            value={inputValue}
                        />
                    </div>
                </GridItem>
            </GridContainer>
            <FloatyAssistant></FloatyAssistant>
        </div>
    );
}

export default withRouter(ChatDefaultPage);